<template>
  <div id="wangjimima">
    <div class="title_info">
      <div class="title_img_div">
        <img src="http://cdn.xqit.net/zg/img/login_logo.png" alt="" class="title_img">
      </div>
      <div class="title_span_div">
        <span class="title_span_div_span_01">玉带职业技术学校教学诊断与改进系统</span>
        <br>
        <span class="title_span_div_span_02">修改密码</span>
      </div>
    </div>
    <div class="input_info">
      <el-form :model="ruleForm2" status-icon :rules="rules2" ref="ruleForm2" label-width="100px" class="demo-ruleForm">
        <el-form-item label="新的密码" prop="pass">
          <el-input type="password" v-model="ruleForm2.pass" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="确认密码" prop="checkPass">
          <el-input type="password" v-model="ruleForm2.checkPass" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm('ruleForm2')">提交</el-button>
          <el-button @click="resetForm('ruleForm2')">重置</el-button>
        </el-form-item>
      </el-form>
    </div>

  </div>
</template>

<script>
  export default {
    data() {
      var validatePass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请输入密码'));
        } else {
          if (this.ruleForm2.checkPass !== '') {
            this.$refs.ruleForm2.validateField('checkPass');
          }
          callback();
        }
      };
      var validatePass2 = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请再次输入密码'));
        } else if (value !== this.ruleForm2.pass) {
          callback(new Error('两次输入密码不一致!'));
        } else {
          callback();
        }
      };
      return {
        ruleForm2: {
          pass: '',
          checkPass: ''
        },
        rules2: {
          pass: [
            { validator: validatePass, trigger: 'blur' }
          ],
          checkPass: [
            { validator: validatePass2, trigger: 'blur' }
          ]
        }
      };
    },
    methods: {
      changePassword(){
        var that = this;
        var linshiuser = sessionStorage.getItem('linshiuser');
        $.ajax({
          type: 'POST',
          url: this.baseUrl+"/ydjxzg/log/getxgpass.action",
          data: {
            username: linshiuser,
            password: this.ruleForm2.checkPass
          },
          dataType: 'json',
          success: function(res){
            if (res.mess) {
              that.$message.success('密码修改成功，即将跳转到登陆界面！')
              setTimeout(function(){
                location.href = that.baseUrl01;
              },3000)
            }else{
              that.$message.error('修改失败！');
            }
          }
        })
      },
      submitForm(formName) {
        var that = this;
        this.$refs[formName].validate((valid) => {
          if (valid) {
            // that.$message.success('密码修改成功，即将跳转到登陆界面！')
            // setTimeout(function(){
            //   location.href = that.baseUrl01;
            // },3000)
            that.changePassword();
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      }
    }
  }
</script>

<style>
  #wangjimima{
    margin-top: 20px;
    position: relative;
  }
  .input_info{
    margin-left: 20px;
    width: 500px;
  }
  .title_info{
    height: 200px;
  }
  .title_img{
    margin-left: 40px;
  }
  .title_img_div{
    float: left;
  }
  .title_span_div_span_01{
    font-size: 20px;
    font-weight: 900;
  }
  .title_span_div_span_02{
    display: block;
    font-size: 20px;
    font-weight: 900;
    margin-top: 100px;
    color: aqua;
  }
</style>



